/* Basic settings */
* {
  box-sizing: border-box;
}

html {
  font-size: 16px;
}

.page {
  min-width: 320px;
  position: relative;
  overflow-x: hidden;
  font-family: 'roboto', Arial, sans-serif;
  font-family: "futura", Arial, sans-serif;
  font-weight: 400;
  font-size: 1rem;
  line-height: 1.75;
  color: #1c1c1c;
}

button, input, textarea, a {
  outline: none;
}

button:focus:required:invalid, input:focus:required:invalid, textarea:focus:required:invalid, a:focus:required:invalid {
  border-color: red;
}

button:required:valid, input:required:valid, textarea:required:valid, a:required:valid {
  border-color: green;
}

button:active, button:focus, input:active, input:focus, textarea:active, textarea:focus, a:active, a:focus {
  border: #666 1px solid;
  outline: none;
}

a {
  text-decoration: none;
}

a:hover {
  text-decoration: none;
}

.container {
  max-width: 1170px;
  margin: 0 auto;
  padding-left: 20px;
  padding-right: 20px;
}

::placeholder {
  color: #666;
}

::selection {
  background-color: #B40BFD;
  color: #fff;
}

.img-responsive {
  display: block;
  max-width: 100%;
  height: auto;
}

.img-responsive.img-width {
  width: 100%;
}

.text-center {
  text-align: center;
}

b, strong {
  font-weight: bold;
}

/* ========= button ======== */
.button {
  display: inline-block;
  width: 100%;
  border: 1px solid #E0003F;
  color: #fff;
  text-decoration: none;
  background-color: transparent;
  padding: 15px 0;
  font-size: 16px;
  font-weight: 400;
  border-radius: 2px;
  text-align: center;
  position: relative;
  outline: none;
  transition: background-color .1s ease;
}

.button:focus, .button:hover {
  text-decoration: none;
  color: #fff;
}

.button:hover {
  background-color: rgba(188, 36, 253, 0.1);
}

.button:active {
  background-color: rgba(166, 2, 237, 0.1);
}

/* ========= nav =========== */
.scroll {
  position: fixed;
  overflow-y: auto;
  left: 0;
  top: 0;
  width: calc(308px - 15px);
  height: 100vh;
  overflow: hidden;
}

.scroll__nav {
  overflow-y: auto;
}

.nav {
  width: 308px;
  height: 100vh;
  max-height: 720px;
  padding: 25px 25px 25px 35px;
  background-color: #30353d;
  color: #ffffff;
  font-family: roboto;
}

.nav__header {
  margin-top: 53px;
  text-align: center;
  white-space: nowrap;
  text-transform: uppercase;
  font-weight: bold;
}

.nav__list {
  margin-top: 37px;
  padding-left: 25px;
}

.nav__button {
  margin-top: 35px;
}

.nav__linkArticles {
  margin-top: 35px;
  display: block;
  text-transform: uppercase;
  font-family: 'times new roman';
  font-weight: 300;
  font-size: 10px;
  text-align: center;
  color: #ffffff;
}

.list__item {
  margin-bottom: 11px;
}

.list__item:last-child {
  margin-bottom: 0;
}

.list__item:hover, .list__item.active {
  font-weight: 100;
  color: #B40BFD;
}

.list__link {
  color: inherit;
}

.list__link:hover {
  text-decoration: none;
  color: inherit;
}

/* ========= logo ========== */
.logo {
  display: flex;
  justify-content: space-around;
  color: #ffffff;
}

.logo:hover {
  text-decoration: none;
  color: #ffffff;
}

.logo__img {
  width: 47px;
  height: 47px;
}

.logo__title {
  font-size: 29px;
  font-weight: bold;
  font-family: roboto;
}

.logo__label {
  font-weight: 300;
  font-family: 'times new roman';
}

.debug-grid {
  height: 100vh;
  position: fixed;
  width: 100%;
  z-index: 1;
}

.debug-grid > div {
  height: inherit;
  max-width: 1170px;
  margin: 0 auto;
  padding-left: 20px;
  padding-right: 20px;
}

.debug-grid > div > div {
  height: inherit;
  display: flex;
  flex-wrap: wrap;
  margin-left: -10px;
  margin-right: -10px;
}

.debug-grid > div > div > div {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 10px;
  margin-right: 10px;
  width: calc(8.33333% - 20px);
  height: inherit;
  outline: #000 1px solid;
  background: rgba(255, 0, 0, 0.2);
}
